/* Login Page */
.loginContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  height: 80vh;
}

.grapl{
  width: 30%; 
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 5rem;
  margin: 3em 1em;
  font-family: "Roboto";
  margin-bottom: 0.75rem;
}

.formContainer {
  background-color: #010101;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.25em;
  box-shadow: 3px 5px 9px #888888;
  box-shadow: 5px 5px 10px #111;
  font-weight: bold;
  padding: 1.5em 2em;
}

.welcomeBanner {
  margin-bottom: 0.5em;
  text-transform: uppercase;
  font-size: 0.8em;
  letter-spacing: 4px;
  color: #bdbdbd;
  text-align: "center";
}

.loginText {
  font-size: 1.2em;
  margin-bottom: 1em;
  text-align: "center";
}
.formElements {
  display: flex;
  flex-direction: column;
  min-width: 350px;
}

.bottomMargin {
  margin-bottom: 0.5em;
}
input{
  border: 1px solid darkgrey;
  width: 85%;
  opacity: 1;
  padding: .2em;
  font-size: 1em;
} 
.formElements input {
  margin: 0;
}
input, 
.submitBtn {
  margin: .5em;
  font-size: 1em;
}

.submitBtn {
  background-color: #238AC0;
  border: none;
  font-weight: bold;
  padding: 0.25em 2em;
  color: white;
  font-size: 1.15rem;
}
@media (max-width: 800px) {
  .backgroundImage {
    flex-direction: column;
  }
  .grapl{
    margin: .5em;
  }
  .formContainer{margin:.5em 2.5em;}
}